import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
	state: {
		hasLogin: false, //
		userInfo: {} //
	},
	//mutations
	mutations: {
		// 
		login(state, user) {
			//
			state.hasLogin = true
			state.userInfo = user
			//
			uni.setStorage({
				key: 'userInfo',
				data: user,
			});
			console.log('')
			console.log(state.hasLogin, state.userInfo)
		},

		// 
		logout(state, user) {
			//
			state.hasLogin = false
			state.userInfo = {}
			//
			uni.removeStorage({
				key: 'userInfo',
			})
			console.log('')
			console.log(state.hasLogin, state.userInfo)
		}
	},
	actions: {

	}
})
export default store
